@charset "utf-8";

.photo-detail {
  box-sizing: border-box;
  position: fixed;
  z-index: 1200;
  width: 100vw;
  height: 100vh;
  padding: 20px;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: stretch;
          align-items: stretch;
  text-align: center;
}

.photo-detail .close-button {
  display: inline-block;
  padding: 10px;
  color: #fff;
}
.button-wrap {
  height: 64px;
  text-align: left;
}
.photo-detail .close-button .material-icons {
  font-size: 30px;
}

.photo-detail > .photo {
  transition: all ease .2s;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  height: calc(100vh - 168px);
}
.photo-detail > .photo.open {
  height: calc(100vh - 272px);
}

.photo-detail > .photo > img {
  width: 100%;
  height: 100%;
  object-fit: scale-down;
}

.info-card {
  font-size: 2rem;
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.8);
}

/* reset */
.photo-detail a {
  color: unset;
}
@charset "utf-8";

.photos-wrap {
  width: 95%;
  margin: 0 auto;
  max-width: 1920px;
}

.photos-wrap .grid-item {
  transition: all ease .5s;
}
.photo-aspectfix {
  position: relative;
  width: 100%;
}
.photo-aspectfix:before {
  content: '';
  display: block;
  padding-top: 75%;
}

.photos-wrap .photo-aspectfix > div {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.list-img {
  object-fit: cover;
  background-color: #eee;
}

@media screen and (min-width: 500px) {
  .photos-wrap > .item {
    /* width: 48%; */
  }
}
